import logo from '~/static/images/profile-logo.svg'
import brand from '~/static/text/brand'
import ContactForm from '../Contact/Form'

export default {
  components: {
    ContactForm
  },
  data: () => ({
    logo: logo,
    brand: brand
  }),
  computed: {
    isMobile() {
      const smDown = this.$store.state.breakpoints.smDown
      return smDown.indexOf(this.$mq) > -1
    }
  }
}
