export default {
  data() {
    return {
      openNotif: false,
      valid: true,
      name: '',
      nameRules: [v => !!v || 'Name is required'],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      message: ''
    }
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.openNotif = true
        console.log({
          name: this.name,
          email: this.email,
          message: this.message
        })
      }
    }
  }
}
